<template>
  <div>
    <div id="top_view">
      <el-row>
        <h3>发布人基本信息</h3>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-col :span="10">
            <span>发布人：</span>
          </el-col>
          <span>{{ name }}</span>
        </el-col>
        <el-col :span="24">
          <el-col :span="10">
            <span>发布手机号：</span>
          </el-col>
          <span>{{ phone }}</span>
        </el-col>
        <el-col :span="24">
          <el-col :span="10">
            <span>服务类型：</span>
          </el-col>
          <span>{{ serviceName }}</span>
        </el-col>
      </el-row>
    </div>
    <div id="middle_view">
      <p>发布需求信息</p>
      <!-- 第一行开始 -->
      <!-- 母婴护理师 -->
      <el-row v-if="parseInt(this.serviceId) === 1">
        <el-col :span="24">
          <label for>预 产 期 ：</label>
          <el-date-picker v-model="dueTime"
                          type="date"
                          placeholder="请选择预产期"></el-date-picker>
        </el-col>
        <el-col :span="24">
          <label for>服务期限：</label>
          <el-select v-model="serviceDaysListId"
                     placeholder="请选择服务期限">
            <el-option v-for="item in serviceDaysList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>宝贝数量：</label>
          <el-select v-model="babyraisingBabyNumListId"
                     placeholder="请选择宝贝数量">
            <el-option v-for="item in babyraisingBabyNumList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 家政工 -->
      <el-row v-if="parseInt(this.serviceId) === 2">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 护工 -->
      <el-row v-if="parseInt(this.serviceId) === 4">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>服务对象：</label>
          <el-select v-model="hulistatusListId"
                     placeholder="请选择服务对象">
            <el-option v-for="item in hulistatusList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 育儿嫂 -->
      <el-row v-if="parseInt(this.serviceId) === 3">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>宝宝数量：</label>
          <el-select v-model="babyNumListId"
                     placeholder="请选择宝宝数量">
            <el-option v-for="item in babyNumList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>宝宝年龄：</label>
          <el-select v-model="babyAgeListId"
                     placeholder="请选择宝宝年龄">
            <el-option v-for="item in babyAgeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 第一行结束 -->
      <!-- 第二行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <label for>服务薪资：</label>
          <el-select v-model="serviceSalaryListId"
                     placeholder="请选择服务薪资">
            <el-option v-for="item in serviceSalaryList"
                       :key="item.id"
                       :label="item.salary"
                       :value="item.id"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>经验要求：</label>
          <el-select v-model="expListId"
                     placeholder="请选择经验要求">
            <el-option v-for="item in expList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label for>年龄要求：</label>
          <el-select v-model="ageListId"
                     placeholder="请选择年龄要求">
            <el-option v-for="item in ageList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 第二行结束 -->
      <!-- 第三行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <div style="display: flex;">
            <label for>服务地址：</label>
            <el-input style="flex: 1;"
                      type="textarea"
                      rows="8"
                      resize="none"
                      maxlength="500"
                      placeholder="请输入服务地址信息"
                      v-model="address"></el-input>
          </div>
        </el-col>
      </el-row>
      <!-- 第三行结束 -->
    </div>
    <div id="bottom_view">
      <div class="new">
        <div id="name"
             class="form-group">
          <p>籍贯要求：</p>
          <div class="bao">
            <div class="native">
              <div v-for="(val, index) in nativeList"
                   v-bind:key="index"
                   :id="val.code"
                   @click="nativeAction(index, val)"
                   :class="nativeListSelect.indexOf(val.code) === -1 ? '' : 'active'">{{val.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="new">
        <div>
          <span>技能要求：</span>
          <span style="color:#999; font-size: 14px;">请选择1-8个最重要的技能标签。</span>
        </div>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in skillList"
                 v-bind:key="index"
                 :id="val.id"
                 @click="skillAction(index, val)"
                 :class="skillListSelect.indexOf(val.id) === -1 ? '' : 'active'">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div class="new">
        <div>
          <span>性格要求：</span>
          <span style="color:#999; font-size: 14px;">请选择1-6个最重要的性格标签。</span>
        </div>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in natureList"
                 v-bind:key="index"
                 :id="val.id"
                 @click="natureAction(index, val)"
                 :class="natureListSelect.indexOf(val.id) === -1 ? '' : 'active'">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div style="display: flex;">
        <label for>备注信息：</label>
        <el-input style="flex: 1;"
                  type="textarea"
                  rows="8"
                  resize="none"
                  maxlength="500"
                  v-model="remark"
                  placeholder="备注信息"></el-input>
      </div>
      <div style="text-align: center; margin-top: 10px;">
        <el-button @click="cancelAction">取消</el-button>
        <el-button type="primary"
                   @click="editAction">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getLoginInfo } from '../../../assets/common/utils'
import {
  getConditionsService,
  getProvinceRegionList,
  getNatureList,
  getServiceBabelList,
  needsInfo,
  editNeeds,
  getNeedsStatus
} from '../../../service/Management.js'
function requestInfo (that) {
  const id = that.$route.params.id
  needsInfo(id).then(res => {
    console.log(res)
    that.name = res.data.publisher_name
    that.phone = res.data.publisher_phone
    that.serviceId = res.data.service_type.id
    that.serviceName = res.data.service_type.title
    if (parseInt(that.serviceId) === 1) {
      that.dueTime = that.getDateYearMonthDay(res.data.due_time)
      console.log(that.dueTime)
      that.serviceDaysListId = res.data.service_date.category
      that.babyraisingBabyNumListId = res.data.baby_num.category
    } else if (parseInt(that.serviceId) === 2) {
      that.serviceModeListId = res.data.live_home.category
    } else if (parseInt(that.serviceId) === 4) {
      console.log('------湖工')
      that.serviceModeListId = res.data.live_home.category
      that.hulistatusListId = res.data.nursin_status.category
      console.log('------湖工')
    } else if (parseInt(that.serviceId) === 3) {
      that.babyNumListId = res.data.baby_num.category
      that.babyAgeListId = res.data.baby_age.category
      that.serviceModeListId = res.data.live_home.category
    }
    that.serviceSalaryListId = res.data.service_salary.id
    that.expListId = res.data.experience_id.category
    that.ageListId = res.data.service_age.category
    that.natureListSelect = res.data.nature.map(item => item.id)
    let region = []
    region = res.data.region
    that.nativeListSelect = region ? region.length === 0 ? ['-1'] : res.data.region.map(item => item.code) : []
    that.skillListSelect = res.data.service_babel.map(item => item.id)
    that.remark = res.data.remark
    if (res.data.addr) {
      var text = ''
      if (res.data.addr.addr) {
        if (res.data.addr.addr) {
          text += res.data.addr.addr
        }
      }
      that.address = text
    } else {
      that.address = ''
    }
    conditionsAction(that)
    serviceBabelAction(that, that.serviceId)
  })
}
function conditionsAction (_this) {
  getConditionsService().then(data => {
    _this.serviceList = data.data.service_type_list
    _this.babyraisingBabyNumList = data.data.babyraising_baby_num_list
    _this.babyNumList = data.data.baby_num_list
    _this.babyAgeList = data.data.baby_age_list
    _this.ageList = data.data.age_list
    _this.expList = data.data.exp_list
    _this.hulistatusList = data.data.hulistatus_list
    _this.serviceModeList = data.data.service_mode_list
    _this.serviceSalaryListAll = data.data.service_salary_list
    _this.serviceDaysList = data.data.service_days_list
  })
  getProvinceRegionList().then(data => {
    _this.nativeList = [{ id: '-1', code: '-1', name: '不限' }]
    _this.nativeList = _this.nativeList.concat(data.data)
  })
  getNatureList().then(data => {
    _this.natureList = data.data
  })
}
// 需求条件之获取技能证书列表
function serviceBabelAction (_this, id) {
  var params = `?service_type_id=${id}&category=skills-label`
  getServiceBabelList(params).then(data => {
    _this.skillList = data.data.items
  })
}
// 发布需求
function eidtNeedsAction (_this) {
  var params = {}
  const id = _this.$route.params.id
  params['id'] = id
  params['publisher_name'] = _this.name
  params['publisher_phone'] = _this.phone
  params['type_id'] = _this.serviceId
  params['service_date'] = parseInt(_this.serviceId) === 1 ? _this.serviceDaysListId : '12'
  params['remark'] = _this.remark
  params['address_id'] = '0'
  params['address'] = _this.address
  params['native_place_code'] = _this.nativeListSelect.join(',')
  params['skill_ids'] = _this.skillListSelect.join(',')
  params['nature_ids'] = _this.natureListSelect.join(',')
  params['housework_company_id'] = getLoginInfo('company_id')
  if (_this.babyAgeListId !== '') {
    params['baby_age'] = _this.babyAgeListId
  }
  if (_this.serviceModeListId !== '') {
    params['live_home'] = _this.serviceModeListId
  }
  if (_this.dueTime !== '') {
    console.log(_this.dueTime)
    params['due_time'] = _this.getDate(_this.dueTime)
  }
  if (_this.babyraisingBabyNumListId !== '') {
    params['baby_num'] = _this.babyraisingBabyNumListId
  }
  if (_this.babyNumListId !== '') {
    params['baby_num'] = _this.babyNumListId
  }
  if (_this.ageListId !== '') {
    params['service_age'] = _this.ageListId
  }
  if (_this.serviceSalaryListId !== '') {
    params['salary_id'] = _this.serviceSalaryListId
  }
  if (_this.expListId !== '') {
    params['experience_id'] = _this.expListId
  }
  // params['housework_company_id'] = getLoginInfo('company_id')
  editNeeds(params).then(data => {
    _this.$confirm(`需求还在下架状态，无法被推荐，请问是否恢复上架？`, '提示', {
      confirmButtonText: '保存修改并上架',
      cancelButtonText: '仅保存修改',
      type: 'warning',
      showClose: false,
      center: true,
      closeOnClickModal: false,
      closeOnPressEscape: false
    }).then(() => {
      _this.shelvesDemand(this)
    }).catch(() => {
      _this.$router.push('/demandRecords')
    })
  })
}
export default {
  mounted () {
    requestInfo(this)
  },
  data () {
    return {
      name: '',
      phone: '',
      address: '',
      remark: '',
      dueTime: '',
      serviceId: '',
      serviceName: '',
      serviceList: [],
      dueDate: '',
      babyNumListId: '',
      babyNumList: [],
      babyraisingBabyNumList: [],
      babyraisingBabyNumListId: '',
      babyAgeList: [],
      babyAgeListId: '',
      ageList: [],
      ageListId: '',
      expList: [],
      expListId: '',
      hulistatusList: [],
      hulistatusListId: '',
      serviceModeList: [],
      serviceModeListId: '',
      serviceSalaryListAll: [],
      serviceSalaryListId: '',
      serviceDaysList: [],
      serviceDaysListId: '',
      nativeListSelect: [],
      nativeList: [],
      natureListSelect: [],
      natureList: [],
      skillListSelect: [],
      skillList: []
    }
  },
  computed: {
    serviceSalaryList () {
      return this.serviceSalaryListAll.filter(
        item => item.service_type_id === parseInt(this.serviceId)
      ).sort(function (a, b) {
        return (a.salary - b.salary)
      })
    }
  },
  methods: {
    shelvesDemand () {
      const id = this.$route.params.id
      const params = `?id=${id}`
      getNeedsStatus(params).then(res => {
        this.$router.push('/demandRecords')
      })
    },
    typeof (obj) {
      return Object.prototype.toString.call(obj)
    },
    timeStrSafari (time) {
      // 全部“-”替换为 “/”
      return time.replace(/-/g, '/')
    },
    getDate (date) {
      // 日期格式转换
      let dateParam = this.typeof(date) === '[object Date]' ? date : this.timeStrSafari(date)
      let d = new Date(dateParam)
      let M = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1)
      let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate()
      let h = d.getHours() < 10 ? '0' + d.getHours() : d.getHours()
      let m = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
      let s = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
      let returnDate = d.getFullYear() + '-' + M + '-' + day + ' ' + h + ':' + m + ':' + s
      return returnDate
    },
    getDateYearMonthDay (date) {
      // 日期格式转换
      let dateParam = this.typeof(date) === '[object Date]' ? date : this.timeStrSafari(date)
      let d = new Date(dateParam)
      let M = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1)
      let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate()
      let returnDate = d.getFullYear() + '-' + M + '-' + day
      return returnDate
    },
    nativeAction (index, val) {
      if (val.code === '-1') {
        let id = val.code
        if (this.nativeListSelect.indexOf('-1') !== -1) {
          this.nativeListSelect = []
        } else {
          this.nativeListSelect = []
          this.nativeListSelect.push(id)
        }
      } else {
        let id = val.code
        let indexTag = this.nativeListSelect.indexOf(id)
        if (this.nativeListSelect.indexOf(id) === -1) {
          if (this.nativeListSelect.indexOf('-1') !== -1) {
            this.nativeListSelect = []
            this.nativeListSelect.push(id)
          } else {
            this.nativeListSelect.push(id)
            if (this.nativeListSelect.length === this.nativeList.length - 1) {
              this.nativeListSelect = ['-1']
            }
          }
        } else {
          this.nativeListSelect.splice(indexTag, 1)
        }
      }
    },
    skillAction (index, val) {
      let code = val.id
      let indexTag = this.skillListSelect.indexOf(code)
      if (this.skillListSelect.indexOf(code) === -1) {
        if (this.skillListSelect.length === 8) {
          this.$message.error('技能标签最多为8个')
          return
        }
        this.skillListSelect.push(code)
      } else {
        this.skillListSelect.splice(indexTag, 1)
      }
    },
    natureAction (index, val) {
      let code = val.id
      let indexTag = this.natureListSelect.indexOf(code)
      if (this.natureListSelect.indexOf(code) === -1) {
        if (this.natureListSelect.length === 6) {
          this.$message.error('性格标签最多为6个')
          return
        }
        this.natureListSelect.push(code)
      } else {
        this.natureListSelect.splice(indexTag, 1)
      }
    },
    editAction () {
      if (parseInt(this.serviceId) === 1) {
        if (this.dueTime === null || this.dueTime === '') {
          this.$message.error('请选择预产期')
          return
        }
        if (this.serviceDaysListId === '' || this.serviceDaysListId === null) {
          this.$message.error('请选择服务期限')
          return
        }
        if (
          this.babyraisingBabyNumListId === '' ||
          this.babyraisingBabyNumListId === null
        ) {
          this.$message.error('请选择宝贝数量')
          return
        }
      } else if (parseInt(this.serviceId) === 2) {
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
      } else if (parseInt(this.serviceId) === 4) {
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
        if (this.hulistatusListId === '' || this.hulistatusListId === null) {
          this.$message.error('请选择服务对象')
          return
        }
      } else if (parseInt(this.serviceId) === 3) {
        if (this.babyNumListId === '' || this.babyNumListId === null) {
          this.$message.error('请选择宝宝数量')
          return
        }
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
      }
      if (
        this.serviceSalaryListId === '' ||
        this.serviceSalaryListId === null
      ) {
        this.$message.error('请选择服务薪资')
        return
      }
      if (this.expListId === '' || this.expListId === null) {
        this.$message.error('请选择经验要求')
        return
      }
      if (this.ageListId === '' || this.ageListId === null) {
        this.$message.error('请选择年龄要求')
        return
      }
      if (this.address === '' || this.address === null) {
        this.$message.error('请输入服务地址')
        return
      }
      if (this.natureListSelect.length === 0) {
        this.$message.error('请选择籍贯要求')
        return
      }
      if (this.skillListSelect.length === 0) {
        this.$message.error('请选择技能要求')
        return
      }
      if (this.nativeListSelect.length === 0) {
        this.$message.error('请选择性格要求')
        return
      }
      eidtNeedsAction(this)
    },
    cancelAction () {
      this.$router.back(-1)
    }
  }
}
</script>

<style scoped>
#top_view,
#middle_view,
#bottom_view {
  text-align: left;
  padding: 0px 15px;
}
.new {
  padding-bottom: 40px;
  text-align: left;
}
.new .bao {
  display: inline-block;
  width: 90%;
}
.new .bao .native,
.nature,
.skillList {
  display: flex;
  flex-wrap: wrap;
}
.new .bao .native div,
.new .bao .nature div,
.new .bao .skillList div {
  /* width: px; */
  padding: 3px 10px;
  height: 28px;
  cursor: pointer;
  margin-right: 12px;
  margin-top: 16px;
  border: 1px solid #f0f0ee;
  border-radius: 17px;
  line-height: 28px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.active {
  border-color: #1cb394 !important;
  color: #1cb394;
}
.el-col label,
.el-col span {
  line-height: 40px;
}
</style>



